import React from 'react';
import { Table, Card } from 'antd';

const source = [
  {
    param: 'title',
    desc: '',
    type: 'string',
    default: '-'
  },
  {
    param: 'content',
    desc: '',
    type: 'ReactNode',
    default: '-'
  },
  {
    param: 'onShow',
    desc: '',
    type: 'function',
    default: '-'
  },
  {
    param: 'onConfirm',
    desc: '',
    type: 'function(hide)',
    default: '-'
  },
  {
    param: 'inline',
    desc: '',
    type: 'boolean',
    default: 'false'
  },
  {
    param: 'hideFooter',
    desc: ' footer',
    type: 'boolean',
    default: 'false'
  },
  {
    param: 'width',
    desc: '',
    type: 'number|string',
    default: 'false'
  },
  {
    param: 'confirmLoading',
    desc: ' loading ',
    type: 'boolean',
    default: 'false'
  },
  {
    param: 'autoHide',
    desc: '',
    type: 'boolean',
    default: 'true'
  }
];

const columns = [
  {
    title: '',
    dataIndex: 'param'
  },
  {
    title: '',
    dataIndex: 'desc'
  },
  {
    title: '',
    dataIndex: 'type'
  },
  {
    title: '',
    dataIndex: 'default'
  }
];

const API = () => (
  <Card>
    <Table bordered pagination={false} dataSource={source} columns={columns} />
  </Card>
);

export default API;



