import React from 'react';
import { Table, Card, Divider } from 'antd';

const querySource = [
  {
    param: 'data',
    desc: '',
    type: ' data : [ row : [ item ] ]',
    default: '-'
  },
  {
    param: 'form',
    desc: ' antd form',
    type: 'object',
    default: '-'
  },
  {
    param: 'handleSearch',
    desc: ' Collection ',
    type: 'function',
    default: '-'
  },
  {
    param: 'handleFormReset',
    desc: ' Collection ',
    type: 'function',
    default: '-'
  }
];

const itemSource = [
  {
    param: 'label',
    desc: '',
    type: 'string',
    default: '-'
  },
  {
    param: 'dataIndex',
    desc: ' form  key',
    type: 'string',
    default: '-'
  },
  {
    param: 'render',
    desc: '',
    type: 'Function(props) {} : ReactNode	',
    default: '-'
  },
  {
    param: 'simple',
    desc: '',
    type: 'boolean',
    default: 'false'
  },
  {
    param: 'options',
    desc: '',
    type: 'object',
    default: '-'
  }
];

const columns = [
  {
    title: '',
    dataIndex: 'param'
  },
  {
    title: '',
    dataIndex: 'desc'
  },
  {
    title: '',
    dataIndex: 'type'
  },
  {
    title: '',
    dataIndex: 'default'
  }
];

const API = () => (
  <Card>
    <h2>Query</h2>
    <Table
      bordered
      pagination={false}
      dataSource={querySource}
      columns={columns}
    />
    <div style={{ marginTop: '1rem' }}> row  item  1 - 3 </div>
    <div style={{ marginTop: '1rem' }}> item </div>
    <div style={{ marginTop: '1rem' }}>
        /   item 
    </div>
    <Divider />
    <h2>item</h2>
    <Table
      bordered
      pagination={false}
      dataSource={itemSource}
      columns={columns}
    />
    <div style={{ marginTop: '1rem' }}>
      
      <a href="https://github.com/react-component/form#option-object">
        rc-form option
      </a>
    </div>
  </Card>
);

export default API;



