import React from 'react';
import { CreateList } from 'mogul';

// 
const mock = {
  name: {
    first: 'bruce',
    last: 'wayne'
  },
  gender: 'male',
  phone: '13333333333',
  email: 'mock@163.com',
  registered: '2018-04-25 14:57',
  location: {
    postcode: '215000'
  }
};
const columns = [
  {
    // label 
    title: '',
    // React  key
    key: 'name',
    // 
    render: ({ name: { first, last } = {} }) => <div>{`${first} ${last}`}</div>
  },
  {
    title: '',
    key: 'gender',
    render: ({ gender }) => <div>{gender === 'female' ? '' : ''}</div>
  },
  {
    title: '',
    //  dataIndex  string  React  key
    dataIndex: 'phone'
  },
  {
    title: 'email',
    dataIndex: 'email'
  },
  {
    title: '',
    dataIndex: 'registered'
  },
  {
    title: '',
    key: 'postcode',
    render: ({ location: { postcode } }) => <div>{postcode}</div>
  }
];

const MockList = () => {
  return CreateList(columns, {
    //  list item label  content 24
    // label  content  8 : 16 1 : 2
    labelCol: 8,
    contentCol: 16,
    // list  item
    column: 2
  })(mock);
};

export default MockList;



