import React from 'react';
import { Table } from 'antd';

const source = [
  {
    prop: 'tool',
    desc: 'Header ',
    type: 'ReactNode',
    default: '-'
  },
  {
    prop: 'routing',
    desc: ' store',
    type: 'object',
    default: '-'
  },
  {
    prop: 'name',
    desc: '',
    type: 'string',
    default: '-'
  },
  {
    prop: 'footer',
    desc: '',
    type: 'string',
    default: '-'
  },
  {
    prop: 'menu',
    desc: 'Sider ',
    type: 'array',
    default: '-'
  },
  {
    prop: 'logo',
    desc: 'Sider logo ',
    type: 'ReactNode',
    default: '-'
  },
  {
    prop: 'url',
    desc: ' match url',
    type: 'string',
    default: '-'
  }
];

const columns = [
  {
    title: '',
    dataIndex: 'prop'
  },
  {
    title: '',
    dataIndex: 'desc'
  },
  {
    title: '',
    dataIndex: 'type'
  },
  {
    title: '',
    dataIndex: 'default'
  }
];

const API = () => (
  <Table bordered pagination={false} dataSource={source} columns={columns} />
);

export default API;



