import React from 'react';
import { Table, Card } from 'antd';

const source = [
  {
    param: 'data',
    desc: '',
    type:
      ' { list : array, pagination : { current : number, pageSize : number, total : number } }',
    default: '-'
  },
  {
    param: 'loading',
    desc: '',
    type: 'boolean',
    default: 'false'
  },
  {
    param: 'buttons',
    desc: '',
    type: 'ReactNode',
    default: '-'
  },
  {
    param: 'columns',
    desc: ' antd  Table columns ',
    type: 'array',
    default: '-'
  },
  {
    param: 'onChange',
    desc: 'Table Change Collection ',
    type: 'function',
    default: '-'
  },
  {
    param: 'selectedRows',
    desc: ' row ',
    type: 'array',
    default: '[]'
  },
  {
    param: 'onSelectRow',
    desc: ' row ',
    type: 'function ( selectedRows ) ',
    default: '-'
  },
  {
    param: 'expandedRowRender',
    desc: '',
    type: 'function ( row ) : ReactNode',
    default: '-'
  },
  {
    param: 'onExpand',
    desc: '+',
    type: 'function ( expand, row ) ',
    default: '-'
  }
];

const columns = [
  {
    title: '',
    dataIndex: 'param'
  },
  {
    title: '',
    dataIndex: 'desc'
  },
  {
    title: '',
    dataIndex: 'type'
  },
  {
    title: '',
    dataIndex: 'default'
  }
];

const API = () => (
  <Card>
    <h2>StandardTable</h2>
    <Table bordered pagination={false} dataSource={source} columns={columns} />
  </Card>
);

export default API;



