module.exports = "# \n\n history \n\n*  URL  index.html  app \n\n http://oursite.com/user/id  404\n\n## \n\n**`Apache`**\n\n```Apache\n<IfModule mod_rewrite.c>\n  RewriteEngine On\n  RewriteBase /\n  RewriteRule ^index\\.html$ - [L]\n  RewriteCond %{REQUEST_FILENAME} !-f\n  RewriteCond %{REQUEST_FILENAME} !-d\n  RewriteRule . /index.html [L]\n</IfModule>\n```\n\n**`nginx`**\n\n```nginx\n#  html \nlocation /template/static {\n    alias /usr/share/nginx/html/tob/template/static;\n}\n\n# html \nlocation /template {\n    root /usr/share/nginx/html/tob/template;\n    index index.html;\n    try_files $uri  /index.html;\n}\n```\n\n**` Node.js`**\n\n```js\nconst http = require('http');\nconst fs = require('fs');\nconst httpPort = 80;\n\nhttp\n  .createServer((req, res) => {\n    fs.readFile('index.htm', 'utf-8', (err, content) => {\n      if (err) {\n        console.log('We cannot open \"index.htm\" file.');\n      }\n\n      res.writeHead(200, {\n        'Content-Type': 'text/html; charset=utf-8'\n      });\n\n      res.end(content);\n    });\n  })\n  .listen(httpPort, () => {\n    console.log('Server listening on: http://localhost:%s', httpPort);\n  });\n```\n\n**` Node.js  Express`**\n\n Node.js/Express [connect-history-api-fallback](https://github.com/bripkens/connect-history-api-fallback) \n\n**`Internet Information Services (IIS)`**\n\n1.   [IIS UrlRewrite](https://www.iis.net/downloads/microsoft/url-rewrite)\n2.   `web.config` \n\n```xml\n<?xml version=\"1.0\" encoding=\"UTF-8\"?>\n<configuration>\n  <system.webServer>\n    <rewrite>\n      <rules>\n        <rule name=\"Handle History Mode and custom 404/500\" stopProcessing=\"true\">\n          <match url=\"(.*)\" />\n          <conditions logicalGrouping=\"MatchAll\">\n            <add input=\"{REQUEST_FILENAME}\" matchType=\"IsFile\" negate=\"true\" />\n            <add input=\"{REQUEST_FILENAME}\" matchType=\"IsDirectory\" negate=\"true\" />\n          </conditions>\n          <action type=\"Rewrite\" url=\"/\" />\n        </rule>\n      </rules>\n    </rewrite>\n  </system.webServer>\n</configuration>\n```\n\n**`Caddy`**\n\n```Caddy\nrewrite {\n    regexp .*\n    to {path} /\n}\n```\n\n**`Firebase `**\n\n `firebase.json` \n\n```json\n{\n  \"hosting\": {\n    \"public\": \"dist\",\n    \"rewrites\": [\n      {\n        \"source\": \"**\",\n        \"destination\": \"/index.html\"\n      }\n    ]\n  }\n}\n```\n"

