module.exports = "# Mogul\n\nMogul (_['mol]_)  ReactMobXAnt Design  Ant Design \n\nMogul \n\n1.   Ant Design  Axure \n1.  \n1.  Mogul CLI \n1.  \n1.  \n1.  \n\n## \n\n```bash\n$ npm i -g mogul-cli\n$ mogul new\n ? type project name please: PROJECT_NAME\n```\n\n## \n\n```bash\nPROJECT_NAME                               # \n  |- src                                   # \n      |- index.js                          # \n      |- index.css                         # \n      |- pages                             # Tip: pages \n          |- App                           # App \n              |- index.js\n          |- Dashboard                     # Dashboard \n              |- index.js\n              |- menu.js                   # Sider \n              |- Tool.js                   # Header \n          |- Sign                          # Sign \n              |- index.js\n          |- Homepage                      # Homepage \n              |- index.js\n      |- utils                             # \n          |- request.js                    #  axios\n          |- env.js                        # \n          |- routing.js                    # mobx router\n          |- token.js                      #  token \n  |- package.json\n  |- README.md                             # \n  |- config-overrides.js                   # rewired \n  |- .eslintrc                             #  eslint \n  |- .prittierrc                           #  prittier \n  |- .gitignore\n```\n\n##  package.json\n\n```json\n{\n  \"name\": \"mogul-template\",\n  \"version\": \"0.0.1\",\n  \"private\": true,\n  \"license\": \"GPL\",\n  \"description\": \"mogul-template\",\n  \"repository\": \"https://github.com/freshesx/mogul-template.git\",\n  \"scripts\": {\n    \"start\": \"react-app-rewired start\",\n    \"build\": \"react-app-rewired build\",\n    \"module\": \"plop --plopfile ./node_modules/mogul/plopfile.js\",\n    \"test\": \"react-app-rewired test --env=jsdom\",\n    \"precommit\": \"lint-staged\"\n  },\n  \"lint-staged\": {\n    \"src/**/*.{js,jsx,json,css}\": [\"prettier --write\", \"git add\"]\n  },\n  \"dependencies\": {\n    \"mogul\": \"^0.1.6\",\n    \"prettier\": \"^1.12.1\"\n  },\n  \"devDependencies\": {\n    \"husky\": \"^0.14.3\",\n    \"lint-staged\": \"^7.0.4\"\n  }\n}\n```\n\n## \n\n```bash\n#  PROJECT_NAME \n$ npm run module\n  ? type module name please: Template\n```\n\n```bash\npages\n  |- App\n  |- Dashboard\n  |- Sign\n  |- Homepage\n  |- Template\n      |- index.js                # \n      |- Collection.js           # \n      |- Query.js                # \n      |- Editor.js               # \n      |- Detail.js               # \n      |- style.module.css        # \n      |- services\n          |- api.js              # \n          |- pagination.js       #  MobX\n          |- query.js            #  MobX\n          |- collection.js       #  MobX\n      |- __test__                # \n```\n\n(Entity) module Project  Project Category module\n\n```bash\n$ npm run module\n ? type module name please: ProjectCategory\n```\n\n```bash\npages\n  |- App\n  |- Dashboard\n  |- Sign\n  |- Homepage\n  |- Projects\n  |- ProjectCategories\n```\n\n## \n\nMogul Router  React Router  Mogul  React Router \n\n### \n\n``\n\n```bash\n                                           # \n/                                          # pages/App/index.js\n  |- /dashboard                            # pages/Dashboard/index.js\n      |- /dashboard/projects               # pages/Projects/index.js\n          |- /dashboard/projects/:id       # pages/Projects/Collection.js\n          |- /dashboard/projects/:id/edit  # pages/Projects/Editor.js\n      |- /dashboard/project-categories     # pages/ProjectCategories/index.js\n  |- /sign                                 # pages/Sign/index.js\n      |- /sign/in                          # pages/Sign/In.js\n      |- /sign/up                          # pages/Sign/Up.js\n```\n\n``\n\n```bash\npages\n  |- App\n      |- index.js\n  |- Dashboard\n      |- index.js\n  |- Projects\n      |- index.js\n      |- Collection.js\n      |- Detail.js\n      |- \n  |- ProjectCategories\n  |- Sign\n```\n\n### \n\n`src/pages/App/index.js`\n\n```js\nimport React from 'react';\nimport { Route, Switch, Redirect } from 'react-router';\nimport { App } from 'mogul';\nimport routing from '../../utils/routing';\nimport Dashboard from '../Dashboard';\n\nexport default () => {\n  return (\n    <App routing={routing}>\n      <Switch>\n        <Redirect from=\"/\" to=\"/dashboard\" exact />\n        <Route path=\"/dashboard\" component={Dashboard} />\n      </Switch>\n    </App>\n  );\n};\n```\n\n`src/pages/Dashboard/index.js`\n\n```js\nimport React from 'react';\nimport { Dashboard } from 'mogul';\nimport { Route, Switch } from 'react-router';\nimport { SYSTEM_FOOTER, SYSTEM_NAME } from '../../utils/env';\nimport routing from '../../utils/routing';\nimport menu from './menu';\nimport styles from './styles.module.css';\nimport Tool from './Tool';\nimport Homepage from '../Homepage';\nimport Template from '../Template';\n// do not delete this Note!\n// insert:component\n\nexport default ({ match: { url } }) => (\n  <Dashboard\n    tool={<Tool />}\n    routing={routing}\n    name={SYSTEM_NAME}\n    footer={SYSTEM_FOOTER}\n    menu={menu}\n    logo={<div className={styles.logo} />}\n    url={url}\n  >\n    <Switch>\n      <Route path={`${url}`} component={Homepage} exact />\n      <Route path={`${url}/template`} component={Template} />\n      {/*do not delete this Note!*/}\n      {/*insert:route*/}\n    </Switch>\n  </Dashboard>\n);\n```\n\n`src/pages/Projects/index.js`\n\n```js\nimport React from 'react';\nimport { Route, Switch } from 'react-router';\nimport Collection from './Collection.js';\nimport Detail from './Detail.js';\nimport Editor from './Editor.js';\n\nconst Routes = ({ match: { url } }) => {\n  return (\n    <Switch>\n      <Route path={url} component={Collection} exact />\n      <Route path={`${url}/:id`} component={Detail} exact />\n      <Route path={`${url}/edit/:id`} component={Editor} exact />\n    </Switch>\n  );\n};\n\nexport default Routes;\n```\n\n## \n\n* [@mogul/cli ](cli.md)\n* [](libraries.md)\n*  Mogul router  ParamsQueryString \n*  MobX \n* \n* [](rules.md)\n*  CURD \n\n## \n\n* [React ](https://doc.react-china.org)\n* [MobX ](http://cn.mobx.js.org)\n* [Ant Design ](https://ant.design/docs/react/introduce-cn)\n"

